@charset "utf-8";

body {
	color: #222;
}
a {
	text-decoration: none;
	color: #222;
}
* {
	box-sizing: border-box;
	padding: 0;
	margin: 0;
}
/* 헤더영역 */
header {
	width: 100%;
	background-color: #fff;
	height: auto;
	position: relative;
}
.header_banner {
	width: 100%;
	height: 42px;
	line-height: 42px;
	text-align: center;
	position: absolute;
	left: 0;
	top: 0;
	background-color: rgb(95, 0, 128);
	color: #fff;
	font-size: 12px;
}
.header_banner strong {
	font-weight: bold;
	font-size: 14px;
}
.h_lnb {
	width: 160px;
	line-height: 12px;
	position: absolute;
	top: 55px;
	right: 320px;
	display: flex;
	justify-content: space-around;
	align-items: center;
}
.h_lnb li {
	border-right: 1px solid #787878;
}
.h_lnb li:last-child {
	border: none;
}
.h_lnb li a {
	font-size: 12px;
}
.header_top {
	width: 1280px;
	height: 150px;
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 0 auto;

	padding-top: 100px;
}
.h_top_l {
	width: 25%;
	display: flex;
	align-items: center;
}
.h_tab {
	padding-left: 20px;
}
.h_tab a {
	font-size: 18px;
	color: #787878;
	border-right: 1px solid #787878;
}
.h_tab a:last-child {
	border: none;
}
.h_active {
	color: rgb(95, 0, 128) !important;
	font-weight: bold;
}
.close_btn {
	position: absolute;
	top: 10px;
	right: 150px;
	font-size: 24px;
	color: #eee;
}
.search_form {
	width: 50%;
	text-align: center;
}
.search_form p {
	position: relative;
}
#search_bar {
	width: 400px;
	height: 48px;
	border-radius: 5px;
	border: 1px solid rgb(95, 0, 128);
	outline: none;
}
.h_search {
	position: absolute;
	font-size: 26px;
	right: 130px;
	top: 11px;
	color: rgb(95, 0, 128);
}
.h_icon {
	display: flex;
	width: 25%;
	justify-content: right;
}
.h_icon li {
	font-size: 30px;
	width: 50px;
	font-weight: 400;
}
.header_bottom {
	width: 1280px;
	height: 50px;
	margin: 0 auto;
	display: flex;
	justify-content: space-between;
	position: relative;
	padding-top: 30px;
	/* box-shadow: rgba(0, 0, 0, 0.07) 0px 3px 4px 0px; */
}
.gnb {
	position: absolute;
	left: 0;
	right: 0;
}
.gnb span {
	display: inline-block;
	margin-left: 5px;
	transform: translateY(-6px);
	font-weight: 600;
}
.gnb_toggle {
	font-size: 24px;
}
.h_menu {
	width: 100%;
	display: flex;
	justify-content: center;
}
.h_menu li {
	width: 100px;
	font-size: 16px;
	font-weight: 600;
}
.h_bottom_right {
	display: inline-block;
	position: absolute;
	top: 20px;
	right: 0;
	width: 150px;
	height: 35px;
	border: 1px solid #ccc;
	border-radius: 30px;
	text-align: center;
	line-height: 30px;
	font-size: 14px;
	color: #787878;
}
.h_bottom_right b {
	color: rgb(95, 0, 128);
	font-weight: bold;
}
main .slide img {
	width: 100%;
}

.slide {
	padding-top: 30px;
}
/* 베스트 상품 */
.best_list {
	padding-top: 50px;
}
.best_list h2 {
	font-size: 24px;
	font-weight: bold;
	text-align: center;
	line-height: 50px;
}
.best_list p {
	font-size: 14px;
	font-weight: 400;
	text-align: center;
}
.best_list > ul {
	width: 70%;
	display: flex;
	margin: 0 auto;
	justify-content: space-between;
	padding-top: 50px;
}
.best_list > ul > li {
	width: 24%;
	position: relative;
}
.best_list > ul > li .event {
	position: absolute;
	top: 10px;
	left: 10px;
	display: inline-block;
	width: 80px;
	height: 30px;
	color: #fff;
	background-color: violet;
	text-align: center;
	line-height: 30px;
	border-radius: 10px;
	font-size: 12px;
	opacity: 0;
	transition: 0.3s;
}
.event_banner {
	padding-top: 50px;
	width: 1280px;
	margin: 0 auto;
}
.event_banner img {
	width: 100%;
	object-fit: cover;
}

.cart_btn {
	display: inline-block;
	border: 1px solid #bbbbbb;
	height: 40px;
	text-align: center;
	line-height: 40px;
	width: 100%;
	color: #333;
	text-decoration: none;
}
.best_list > ul > li:hover .event {
	opacity: 1;
}
.best_list > ul > li img {
	vertical-align: middle;
	width: 100%;
}
.txt_box {
	width: 100%;
	line-height: 160%;
	padding-top: 10px;
}
.txt_box h3 {
	font-size: 18px;
	line-height: 130%;
	font-weight: 500;
}
.txt_box del {
	color: #787878;
	font-size: 14px;
}

.txt_box p {
	text-align: left;
	font-size: 20px;
	font-weight: bold;
	color: red;
}
.txt_box h4 {
	font-size: 24px;
	font-weight: bold;
}
footer {
	background-color: #222;
	text-align: center;
	padding: 10px 0;
	color: #fff;
}

footer > address {
	font-style: normal;
	color: #fff;
	line-height: 60px;
}
